
import { Component, OnInit } from '@angular/core';
import { ProjService } from '../proj.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-college',
  templateUrl: './college.component.html',
  styleUrls: ['./college.component.css']
})
export class CollegeComponent implements OnInit {
  collegeInfo: any;
  stateName:any;
  type:any;
  i :any;
  len:any;
  details:any[] = new Array();

  constructor(private service: ProjService, private router:Router) { }

  ngOnInit(): void {
    this.service.getCollegeInfo().subscribe((data: any) => {console.log(data); this.collegeInfo = data.data.medicalColleges;});
  }

  getDetails() {
    this.deleteOf();
    this.i = 0;
    console.log(this.stateName, this.type)
    for( const info of this.collegeInfo){
      
      if(info.state == this.stateName && info.ownership == this.type){
        //console.log("hello")
        this.details.push(info);

        this.i++;
      }
    }
    console.log(this.details);
    //this.router.navigate(['college']);
}
getAllDetails(){
  this.details = this.collegeInfo;
  this.router.navigate(['college']);

}

deleteOf(){
  this.len = this.details.length;
  for( this.i = 0;this.i < this.len;this.i++){
    this.details.pop();
  }

}

}